@charset "UTF-8";
/* フォームリセット
---------------------------------------------------------------------------- */
form {
  margin: 0;
  padding: 0;
}

label {
  display: block;
  cursor: pointer;
}

fieldset,
legend {
  margin: 0;
  padding: 0;
  border: none;
  border-radius: 0;
  outline: none;
  background: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

input,
select,
optgroup,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  border: none;
}

input[type="date"],
input[type="datetime-local"],
input[type="month"],
input[type="time"] {
  padding: 0.25rem;
  border: 1px solid #ccc;
  outline: none;
  font-size: 1rem;
}

input[type="text"],
input[type="password"],
input[type="email"],
input[type="tel"],
input[type="url"],
input[type="search"],
input[type="number"],
input[type="datetime"],
input[type="week"],
textarea,
select {
  margin-right: 0;
  margin-left: 0;
  background-image: none;
  border-radius: 0;
  outline: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

textarea {
  display: block;
  width: 100%;
  height: 6.25rem;
  overflow: auto;
  -ms-overflow-style: scrollbar;
  resize: vertical;
}

select {
  padding-right: 1.875rem;
  background-image: url("data:image/png;base64,");
  background-repeat: no-repeat;
  background-size: 0.75rem 0.75rem;
  background-position: right 0.5rem center;
  text-transform: none;
}

.select {
  display: inline-block;
  position: relative;
  overflow: hidden;
  background: #fff;
  cursor: pointer;
}

.select select {
  width: 100%;
  padding-right: 1.5rem;
  border: 1px solid #ccc;
  background: none;
  cursor: pointer;
}

.select::before {
  position: absolute;
  top: 0.8125rem;
  right: 0.5rem;
  width: 0;
  height: 0;
  padding: 0;
  content: "";
  border-left: 0.375rem solid transparent;
  border-right: 0.375rem solid transparent;
  border-top: 0.375rem solid #666;
  pointer-events: none;
}

/* radio & checkbox */
input[type="radio"],
input[type="checkbox"] {
  display: none;
}

input[type="radio"] + span,
input[type="checkbox"] + span {
  display: inline-block;
  position: relative;
  color: #1b224c;
  cursor: pointer;
}

input[type="radio"] + span::before,
input[type="checkbox"] + span::before {
  position: absolute;
  left: 0;
  width: 0.375rem;
  height: 0.375rem;
  content: "";
  border: 1px solid #1b224c;
  background: #fff;
}

/* fieldset */
fieldset {
  padding: 0.5rem 0.375rem;
  border: 1px solid #ccc;
}

legend {
  padding: 0 0.5rem;
}

/* button */
input[type="submit"] {
  -webkit-appearance: none;
  background-color: inherit;
  background-image: none;
  display: inline-block;
  margin-top: 0;
  margin-bottom: 0;
  text-decoration: none;
}

input[type="submit"]::-moz-foucus-inner {
  border: none;
  padding: 0;
}

input[type="reset"],
input[type="button"] {
  display: inline-block;
  padding: 0.625rem 1.875rem;
  border: 1px solid #999;
  border-radius: 0;
  -webkit-box-sizing: content-box;
  -webkit-appearance: button;
  appearance: button;
  color: #000;
  font-size: 1.25rem;
  cursor: pointer;
}

/* ブラウザ対策
---------------------------------------------------------------------------- */
/* IE */
select::-ms-expand {
  display: none;
}

/* iOS */
input[type="submit"]::-webkit-search-decoration,
input[type="reset"]::-webkit-search-decoration,
input[type="button"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-decoration {
  display: none;
}

::-webkit-search-cancel-button {
  -webkit-appearance: none;
}

input[type="search"]::-webkit-search-decoration {
  margin: 0;
  padding: 0;
  border: 0;
  border-radius: 0;
  -moz-appearance: none;
  -webkit-appearance: textfield;
  -webkit-box-sizing: border-box;
  appearance: none;
  background: none;
  outline: none;
  font-size: 0.375rem;
}

/* 挙動
---------------------------------------------------------------------------- */
/* focus */
input[type="submit"]:focus,
input[type="reset"]:focus,
input[type="button"]:focus,
input[type="search"]:focus {
  outline-offset: -2px;
}

/* disabled */
input:disabled,
input:disabled:hover,
textarea:disabled,
textarea:disabled:hover {
  border-color: #ccc;
  background: #eee;
  cursor: not-allowed;
}

input[type="radio"]:disabled + span,
input[type="checkbox"]:disabled + span {
  color: #ccc;
  cursor: not-allowed;
}

input[type="radio"]:disabled + span::before,
input[type="checkbox"]:disabled + span::before {
  border-color: #ccc;
  cursor: not-allowed;
}

/* placeholder */
input::placeholder,
textarea::placeholder {
  color: #b4b4b4;
  font-size: 0.75rem;
}

/* リセットCSS
---------------------------------------------------------------------------- */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}

@media (max-width: 1000px) {
  html {
    font-size: 1.6vw;
  }
}

@media screen and (max-width: 767px) {
  html {
    font-size: 16px;
  }
}

@media (max-width: 375px) {
  html {
    font-size: 4.26667vw;
  }
}

html {
  scroll-behavior: smooth;
}

:target {
  scroll-margin-top: 60px;
}

html,
body {
  margin: 0;
  padding: 0;
}

body {
  font-family: "";
  font-size: 1rem;
  font-weight: 400;
  color: #111;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fff;
  overflow-wrap: break-word;
  min-height: 100vh;
  min-height: -webkit-fill-available;
  position: relative;
  z-index: -9999;
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

a {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
  transition: all 0.3s ease-in-out;
}

a:hover {
  opacity: 0.8;
}

@media screen and (max-width: 767px) {
  a:hover {
    opacity: 1;
  }
}

img {
  display: block;
  max-width: 100%;
  height: auto;
  border: none;
  object-fit: cover;
  object-position: center;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 400;
}

a,
dl,
dt,
dd,
article,
aside,
details,
figcaption,
figure,
picture,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

h1,
h2,
h3,
h4,
h5,
h6,
a,
p,
span,
em,
small,
strong,
sub,
sup,
mark,
del,
ins,
strike,
abbr,
dfn,
blockquote,
q,
cite,
code,
pre,
ol,
ul,
li,
dl,
dt,
dd,
div,
section,
article,
main,
aside,
nav,
header,
hgroup,
footer,
img,
figure,
figcaption,
address,
time,
audio,
video,
canvas,
iframe,
details,
summary,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  margin: 0;
  padding: 0;
  border: 0;
}

button {
  display: inline-block;
  text-decoration: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: none;
  background-color: transparent;
  cursor: pointer;
  margin: 0;
  padding: 0;
  border-radius: 0;
  outline: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  text-transform: none;
}

video {
  height: auto;
  border: none;
}

iframe,
object {
  max-width: 100%;
  height: auto;
  border: none;
}

ul,
ol {
  list-style: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
  table-layout: fixed;
}

th,
td {
  padding: 4px 6px;
  text-align: left;
  vertical-align: top;
  border: 1px solid;
}

blockquote,
q {
  quotes: none;
}

blockquote::after,
blockquote::before,
q::after,
q::before {
  content: "";
  content: none;
}

address {
  font-style: normal;
}

b,
strong {
  font-weight: 700;
}

a,
area,
button,
input,
label,
select,
summary,
textarea,
[tabindex] {
  touch-action: manipulation;
}

dialog {
  background-color: inherit;
  border: solid;
  color: inherit;
  display: block;
  height: fit-content;
  left: 0;
  margin: auto;
  padding: 1em;
  position: absolute;
  right: 0;
  width: fit-content;
}

dialog:not([is-open]) {
  display: none;
}

summary {
  display: list-item;
  cursor: pointer;
}

